@charset "utf-8";

/*importing colors from _colors.scss*/
@import "colors";

/* set colors from colors.scss */
$link-color: $white;

/* base variables pulled from theme.yml */
$base-font-family: "Hind", sans-serif;
$base-font-size: 16px;
$text-color: #191919;
$heading-font-family: "Georgia", serif;
// $link-color: ""; // commenting out to use $link-color from _colors.scss

/* theme colors pulled from config-theme-colors */




$primary: #ffffff;


$success: $burnt;

$theme-colors: () !default;
$theme-colors: map-merge(
  (
    
    "primary": $primary,
    
    "success": $success
    
  ),
  $theme-colors
);
@import "theme-colors", "theme-utilities";


@import "base", "pages", "custom";


// Branding wrapper layout styles
.branding-wrapper {
  padding: 1rem 1rem 1rem 0;
  text-align: center;
}

.branding-wrapper svg {
  max-width: 100%;
  height: auto;
  padding-left: 3rem;
}

.bg-custom-color {
  background-color: #f2f2f2;
}
.nav-custom-color{
  background-color: #ffffff;
}

