/* - - - - - 
BASE STYLES
- - - - - - */

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: linear-gradient(to right, #8c8782 28%, #cccccc 0);
}

/* this loads and hides the banner image for better lighthouse score. real image loads through css */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  white-space: nowrap;
  border: 0;
}

/* - - - -  
BANNERS & TITLES
- - - - - */

/* adds a sepia overlay with some opacity to the banner image */
.bg-sepia {
  background-color: #31261dB3 !important;
}

/* another overlay color option in case team doesn't like sepia */
.bg-black {
  background-color: #00000099 !important; 
}

.site-title-container {
  margin-top: 2.5rem;
}

.site-title, .section-title, .site-title-default {
  @if $heading-font-family != "" { font-family: $heading-font-family; }
}

/* styles for the site title on the home page banner */
h1.site-title {
    font-size: 3.5rem;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

/* style for subtitle of exhibit on home page banner */
h2.site-title{
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

/* styles for the site title on all other pages using the default template */
h1.site-title-default {
  font-size: 2.5rem;
  text-align: center;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
}

/* margin specific to the title on the homepage. doesn't apply to default template */
.title-margin {
  margin: 28px 0px 99px 0px;
}

/* section header used only on the homepage */
h2#explore-the-exhibit {
  margin: 20px 0px 30px 0px;
  text-align: center;
  @if $heading-font-family != "" { font-family: $heading-font-family; }
}

/* fixes margins for title containers on devices 767 and smaller */
@media (max-width: 767px) {

  .site-title-container {
    margin: 0px 0px 50px 0px;
  }

}
 
/* - - - - 
NAVIGATION
- - - - - */
/* styling the hamburger menu on mobile */
button.navbar-toggler {
  background-color: #ffffff99;
}

/* left align menu items on mobile */
@media (max-width: 991.98px) {
  .navbar-collapse .navbar-nav {
    text-align: left !important;
    align-items: flex-start !important;
    justify-content: flex-start !important;
  }

  .navbar-collapse .nav-link {
    text-align: left !important;
  }
}


/* - - - -
LOGO
- - - - - */

#logo {
  max-width: 350px;
  min-height: 45px; 
  margin: 2.5rem;
  shape-rendering: geometricPrecision;
  text-rendering: geometricPrecision;
  image-rendering: auto;
  fill: #ffffff;
}

  #logo path.caltech-logotype {
    fill: #FF6C0C;
  }

  div.no-outline {
    border:none !important;
  }

/* Override link colors for child items */
a.dropdown-custom {
  color:#000000;
  border-bottom: dotted #eeeeee 1px;
  display: block;
  padding: 5px 0px 5px 15px;
  position: relative; // optional, for fine control
  width:250px;
}

a.dropdown-custom::before {
  content: "▶"; 
  margin-right: 8px;
  font-size: 0.8em;
  color: #cbe3df;
}

/* transferring the styles from main menu to ul */
ul.custom-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

ul.custom-list li {
  color: #000000;
  border-bottom: dotted #eeeeee 1px;
  padding: 5px 0px 5px 15px;
  position: relative;
  width: 250px;
  display: block;
}

ul.custom-list li::before {
  content: "▶";
  margin-right: 8px;
  font-size: 0.8em;
  color: #cbe3df;
  position: absolute;
  left: 0;
}

/* Custom Exhibit Color Schemes for main menu*/
.burnt:hover {
  background-color: $burnt;
  color:#000000 !important;
}

a.black:hover {
  color:#000000 !important;

}

.lions-main:hover {
  background-color: $lions-main;
}

.mission-trail:hover {
  background-color: $mission-trail;
}

.pencil-lead:hover {
  background-color: $pencil-lead;
}

.campfire:hover {
  background-color: $campfire; 
}

/* - - - - -
BUTTONS
- - - - - */

/* using grey because there are a variety of colors happening on homepage cards where this is predominant. */
.btn-primary {
  background-color: #cccccc;
  border-color:  #c5c5c5;
}

.btn-primary:hover,
.btn-primary:focus {
  background-color: #ababab; 
  border-color: #ababab;
}

/* Optional: style plain .btns if used without btn-primary */
.btn {
  font-family: 'Hind', sans-serif;
  border-radius: 4px;
  text-transform: uppercase;
  font-weight: 600;
}

/* - - - - - 
CONTAINERS
- - - - - */

.body-container {
  color:#000000;
  padding: 3rem 4rem;
  line-height: 2rem;
  margin: 4rem;
