/* - - - - - -
Mirador Styles
- - - - - - - */

/* div that acts as container for mirador player */
#my-mirador {
  height: 900px;
  max-width: 95%;
  margin-top: 43px;
  position: relative;
}

.my-mirador-community {
  margin: 0 3.5rem 0 3.5rem !important;
  max-width: 100% !important;
}

/* fixes margins for mirador container on devices 1199 and smaller */
@media (max-width: 1199px) {
  .osd-shell {
    margin-top: 0px !important; 
  }
}




/* fixes margins for mirador container on devices 990 and smaller */
@media (max-width: 990px) {
  #my-mirador {
    max-width: 100%;
    margin: 65px 35px 0px 45px;
    position: relative;
  }

  .my-mirador-community {
    max-width: 100% !important;
  }
}

/* fixes margins for mirador container on devices 767 and smaller */
@media (max-width: 767px) {
  #my-mirador {
  height: 900px;
  max-width: 100%;
  margin: 65px 5px 0px 5px !important;  
  position: relative;
  }

  .my-mirador-community {
  margin: 40px 5px 0px 5px !important;
  max-width: 100% !important;
}
}

/* fixes margins for mirador container on devices 540 and smaller */
@media (max-width: 540px) {
  #my-mirador {
  height: 900px;
  max-width: 100%;
  margin: 30px 5px 0px 5px !important;
  position: relative;
  }

  .my-mirador-community {
  margin: 20px 0px 0px 0px !important;
  max-width: 100% !important;
}
}

/* fixes margins for mirador container on devices 445 and smaller */
@media (max-width: 445px) {
  #my-mirador {
  height: 900px;
  max-width: 100%;
  margin: 0px 5px 0px 5px !important;
  position: relative;
  }

  .my-mirador-community {
  margin: 20px 0px 0px 0px !important;
  max-width: 100% !important;
}
}

/* Image sizing inside gallery tiles (preserve aspect) */
#my-mirador .mirador-viewer section[id^="window-"][id$="-gallery"] img.mirador63 {
  width: auto !important;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}

/* Caption spacing inside gallery tiles */
#my-mirador .mirador-viewer section[id^="window-"][id$="-gallery"] .mirador67.mirador69 {
  margin-top: 6px;
}


/* Adds styling specific to thumbnail navigation when visible to respect aspect ratio */
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] { overflow: hidden; }
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] img {
  max-width: 100%;
  max-height: 100%;
  width: auto;       
  height: auto;     
  display: block;
  object-fit: contain;
}

/* Constrain nav thumbnails to cell; center without squishing */
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] img {
  max-width: 100% !important;
  max-height: 100% !important;
  width: auto !important;  
  height: auto !important;  
  display: block !important;
}

/* STRONG clamp: override inline width/height on nav thumbnails */
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] > * img,
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] img,
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] button img {
  max-width: 100% !important;
  max-height: 100% !important;
  width: auto !important;
  height: auto !important;
  object-fit: contain !important;
  display: block !important;
}
.mirador-viewer {
  background: none !important;
}


/* - - - - - - - - 
Open Seadragon
- - - - - - - - - */

/* container - includes everything */
.osd-shell {
  height: auto;                 
  background-color: #ffffff;
  padding: 1rem;
  margin-top: 65px;
  border-top: 2px solid $lions-main;
  max-width: 95%;
}

.multimedia-osd-shell {
  padding: 1rem;
  border-top: 2px solid $campfire !important;
  margin: 0px 4rem 0px 4rem !important;
}

/* gallery title */
.osd-header h1 {  
  font-family: "Georgia", "serif";
  font-size: 2rem;
  margin-bottom: 10px;
}

/* Prevent images from hijacking drag gestures */
.osd-thumbs img {
  -webkit-user-drag: none;
  user-select: none;
}

/* container for metadata and viewer */
.osd-body-container {
  border: 1px solid #eee;
  background: #fff;
}

/* VIEWER */

#osd-viewer {
  height: 72vh;                  
}

/* METADATA */

.osd-meta {
  overflow: auto;
  padding: 1.5rem 1rem 0 1.5rem;
  background: #fff;
  border-left: 1px #eee solid;
}

.osd-meta h2{
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

/* NAVIGATION */

.osd-nav-container {
  border: 1px solid #eee;
  background: #eee;
}

.osd-thumbs {
  padding: 13px 0;
  touch-action: pan-x;            
  scrollbar-gutter: stable;  
  margin-bottom: 0px;   
  background: #eee;  
}

.thumb {
  height: 100px;
  border: 1px solid #ccc;
  background: #fff;
  cursor: pointer;
}

#thumbs {
  scrollbar-width: thick;           
  scrollbar-color: #e6af48 #f8f8f8;   
}

#thumbs::-webkit-scrollbar {
  height: 16px;
}

.thumb img { 
  max-width: 100%; 
  max-height: 100%; 
  display: block;
  padding: 3px 0px 3px 0px;
 }

.thumb[aria-current="true"] { 
  background: $lions-main; 
}

.multimedia-osd-shell .osd-nav-container .osd-thumbs .thumb[aria-current="true"] {
  background-color: $campfire !important;
}


/* Responsive viewer height tweaks */
@media (max-width: 1199 px) {   
  
  #osd-viewer { 
    height: 60vh; 
  }
}

@media (max-width: 767.98px) {
  #osd-viewer { 
    height: 50vh; 
  }
  .thumb { 
    width: 120px; 
    height: 84px; 
  }
}

@media (max-width: 767px) {
    .multimedia-osd-shell {
        margin: 50px 10px 0px 10px !important;
    }
}


#iiif-shell, .osd-shell, .container-fluid {
  overflow-x: visible !important;
}
 
/* adujusting padding on mirador container at 445 and smaller */
@media (max-width: 445px) {
