#my-mirador .mirador-thumb-navigation [role="gridcell"] button img,
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] > * img,
#my-mirador .mirador-window-thumbnail-navigation [role="gridcell"] img,
